const Model = props => {
  const { className, onClick } = props
  const cl = bem('model', className)

  const stageWidth = 152
  const stageGutter = 10
  const stageOffset = 0

  const dimensionWidth = 340
  const dimensionHeight = 30
  const dimensionGutter = 0
  const dimensionOffset = 410

  const origin = {
    x: 50,
    y: 420,
  }

  const stages = [
    {
      name: 'Reporting',
      icon: (
        <Chart className={cl('icon', '$chart').toString()} x="100" y="400" />
      ),
    },
    {
      name: 'Analyzing',
      icon: (
        <Magnify
          className={cl('icon', '$magnify').toString()}
          x="100"
          y="300"
        />
      ),
    },
    {
      name: 'Optimizing',
      icon: <Gear className={cl('icon', '$gear').toString()} />,
    },
    {
      name: 'Empowering',
      icon: <Fist className={cl('icon', '$fist').toString()} />,
    },
    {
      name: 'Innovating',
      icon: <Bulb className={cl('icon', '$bulb').toString()} />,
    },
  ]

  const dimensions = [
    'Data',
    'Metrics',
    'Skills',
    'Technology',
    'Leadership',
    'Culture',
    'Strategy',
    'Agility',
    'Integration',
    'Empowerment',
  ]

  const gradients = [
    '#e17055',
    '#fdcb6e',
    '#00b894',
    '#00cec9',
    '#0984e3',
    '#6c5ce7',
  ]

  const dimensionGradients = [
    '#00b894',
    '#00cec9',
    '#6c5ce7',
    '#0984e3',
    '#e17055',
    '#fdcb6e',
    '#d63031',
    '#e84393',
    '#558B2F',
    '#8BC34A',
  ]

  return (
    <svg viewBox="0 0 800 400">
      <defs>
        <clipPath id="myClip">
          <path
            d="M0 300 Q 400 300 800 0 L 800 400 L 0 400 L 0 300"
            stroke="black"
            strokeWidth="1"
            fill="transparent"
          />
        </clipPath>

        <path id="capPath" d="M-25,-50 L25,0 L-25,50" fill="none" />

        {gradients.map((gradient, index) => (
          <linearGradient
            id={`gradient-${index}`}
            x1="0%"
            y1="0%"
            x2="100%"
            y2="100%"
            key={index}
          >
            <stop offset="0%" stopColor={gradient} />
            <stop offset="100%" stopColor={gradients[index + 1]} />
          </linearGradient>
        ))}

        {Array(5)
          .fill(true)
          .map((_, index) => (
            <g key={`index-dimGradient-${index}`}>
              <linearGradient
                id={`dimensionGradient-${index}-left`}
                x1="0%"
                y1="50%"
                x2="100%"
                y2="50%"
                key={`dimensionGradient-${index}-left`}
              >
                <stop offset="0%" stopColor={dimensionGradients[index * 2]} />
                <stop
                  offset="100%"
                  stopColor={dimensionGradients[index * 2 + 1]}
                />
              </linearGradient>

              <linearGradient
                id={`dimensionGradient-${index}-right`}
                x1="100%"
                y1="50%"
                x2="0%"
                y2="50%"
                key={`dimensionGradient-${index}-right`}
              >
                <stop offset="0%" stopColor={dimensionGradients[index * 2]} />
                <stop
                  offset="100%"
                  stopColor={dimensionGradients[index * 2 + 1]}
                />
              </linearGradient>
            </g>
          ))}
      </defs>

      {stages.map((stage, index) => (
        <g
          className={cl('stage', {
            $faded:
              props.highlight && stage.name.toLowerCase() !== props.highlight,
          }).toString()}
          key={`stage-${index}`}
        >
          <rect
            className={`model__stage-rect model__stage-rect--${stage.name.toLowerCase()}`}
            x={stageOffset + index * (stageWidth + stageGutter)}
            y="0"
            width={stageWidth}
            height="400"
            clipPath="url(#myClip)"
          />
          <text
            className={cl('stage-name').toString()}
            x={
              stageOffset + index * (stageWidth + stageGutter) + stageWidth / 2
            }
            y={380}
            fill="white"
            alignmentBaseline="middle"
            textAnchor="middle"
          >
            {stage.name}
          </text>
          {stage.icon}
        </g>
      ))}

      {props.maturityScore && (
        <rect
          width="800"
          height="400"
          x={160 * props.maturityScore}
          clipPath="url(#myClip)"
          fill="#FFFFFF"
          fillOpacity="0.7"
        />
      )}
    </svg>
  )
}

export default Model
